@import "./fonts";
@import "./colors";

body {
  background-color: $color-backgroundgray;
  font-family: $font-sans;
}

.CitationDB {
  font-family: $font-sans;

  .BreadCrumb {
    color: $color-textgray;
    font-size: 14px;
    // font-family: $font-serif;

    .tray {
      display: flex;
      .Button {
        margin-right: 4px;
      }

      .PinsButton .Button {
        background-color: $color-author-off;
      }
      .PinsButton .Button:hover {
        background-color: $color-author-on;
      }

      .HomeButton .Button{
        background-color: $color-resource-on;
      }


    }

    .nav-separator,
    .nav-item {
      margin-right: 1em;
      border: 1px solid $color-white;
      padding: 2px;
      border-radius: 3px;
      vertical-align: middle;
    }

    .nav-item:hover,
    .nav-separator:hover {
      background-color: $color-white;
    }
  }

  section {
    position: relative;
    width: 80%;
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
  }

  .module-box .title {
    font-family: $font-serif;
  }

  .ResultListItem.footnote {
    align-items: center;
    justify-content: space-between;
    padding-right: 18px;

    .Button {
      min-width: 80px;
    }
  }

  .PublicationHistogram rect.bar {
    fill: $color-publication-on;
  }

  .HomePage {
    .TextInput {
      text-align: center;

      input {
        background-color: $color-white;
      }
    }
  }

  a {
    text-decoration: none;
    color: $color-link-off;
  }

  a:hover {
    color: $color-link-on;
  }

  .page-tray {
    display: flex;
    align-items: center;
  }

  .ResultListItem {
    .SaveButton.save {
      top: -100px;
    }
  }

  .ResultListItem:hover{
    .SaveButton {
      top: 0px;
      transition: top .25s ease-in-out;
    }
  }

  // .page-tray{
  .SaveButton {
    position: absolute;
    top: 0px;
    right: 0px;
  }
  // }

  .ResultListItem .SaveButton .Button {
    border-radius: 0px 8px 0px 8px;
    padding: 0px 6px;
  }
  .SaveButton.save .Button {
    background-color: $color-publication-off;
  }

  .SaveButton.save .Button:hover {
    background-color: $color-publication-on;
  }

  .SaveButton.unsave .Button {
    background-color: $color-author-off;
  }

  .SaveButton.unsave:hover .Button {
    background-color: $color-author-on;
  }

  .ToggleButton.publications.on .Button {
    background-color: $color-publication-on;
  }

  .ToggleButton.resources.on .Button {
    background-color: $color-resource-on;
  }

  .ToggleButton.authors.on .Button {
    background-color: $color-author-on;
  }
}
