@import url(https://use.typekit.net/wjj5bcz.css);
.ResultListItem.publication {
  border-color: #8ec8cc; }

.ResultListItem.author {
  border-color: #f48734; }

.ResultListItem.resource,
.ResultListItem.footnote {
  border-color: #fce188;
  display: flex; }
  .ResultListItem.resource .Button,
  .ResultListItem.footnote .Button {
    background-color: #8ec8cc;
    -webkit-transition: background-color 1s;
    transition: background-color 1s; }

.ResultListItem.publication:hover {
  border-color: #0d99aa; }

.ResultListItem.author:hover {
  border-color: #ca6251; }

.ResultListItem.resource:hover,
.ResultListItem.footnote:hover {
  border-color: #f9be00; }
  .ResultListItem.resource:hover .Button,
  .ResultListItem.footnote:hover .Button {
    background-color: #0d99aa; }

.ResultListItem:hover {
  box-shadow: 1px 1px 10px #aaaaaa;
  border-width: 20px;
  -webkit-transition: border-width 0.2s;
  transition: border-width 0.2s;
  max-width: 100%; }
  .ResultListItem:hover .pill-tray-wrapper.resources .pill {
    border-color: #f9be00; }
  .ResultListItem:hover .pill-tray-wrapper.publications .pill {
    border-color: #0d99aa; }
  .ResultListItem:hover .pill-tray-wrapper.author .pill {
    border-color: #ca6251; }
  .ResultListItem:hover .pill-tray-wrapper .pill {
    max-width: 100%;
    box-sizing: border-box;
    -webkit-transition: max-width 1s;
    transition: max-width 1s; }

.ResultListItem {
  box-sizing: border-box;
  overflow: hidden;
  border-left: 10px solid white;
  position: relative;
  /**
   * Define the content formatting that's specific to this app
   **/
  display: flex; }
  .ResultListItem header small.result-type {
    font-size: 10px;
    font-weight: normal; }
  .ResultListItem .content-area {
    max-width: 100%;
    padding: 14px;
    flex: auto;
    align-items: center; }
    .ResultListItem .content-area header h3 {
      margin: 0px;
      padding: 0px;
      font-size: 18px;
      line-height: 18px;
      margin-bottom: 6px; }
  .ResultListItem footer {
    font-size: 14px;
    color: #444; }
  .ResultListItem .pill-tray {
    display: flex;
    flex-wrap: wrap;
    max-width: 100%; }
    .ResultListItem .pill-tray .pill {
      background-color: #f5f5f5;
      -webkit-transition: border-color 3s;
      transition: border-color 3s;
      margin: 0px 3px;
      padding: 0px 4px;
      border-radius: 2px;
      font-size: 11px;
      max-width: 100px;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      margin-top: 3px; }
  .ResultListItem .pill-tray-wrapper .pill {
    border-left: 3px solid white; }
  .ResultListItem .pill-tray-wrapper.resources .pill {
    border-color: #fce188; }
  .ResultListItem .pill-tray-wrapper.publications .pill {
    border-color: #8ec8cc; }
  .ResultListItem .pill-tray-wrapper.author .pill {
    border-color: #f48734; }
  .ResultListItem .ItemHeader {
    font-weight: 700;
    display: flex;
    align-items: first baseline;
    margin-bottom: 11px; }
  .ResultListItem .badge {
    font-weight: 400;
    font-size: 10px;
    text-transform: uppercase;
    margin-right: 6px;
    padding-right: 3px; }
  .ResultListItem .HeaderLink {
    display: flex;
    justify-content: space-around; }

@media only screen and (max-width: 500px) {
  .ResultListItem .ItemHeader {
    display: block; } }

.Button {
  display: flex;
  padding: 0px 10px;
  background-color: #efefef;
  flex-direction: column;
  justify-content: center;
  font-size: small;
  font-weight: normal;
  text-align: center;
  height: 30px;
  border-radius: 8px;
  font-weight: bold; }

.Button,
a .Button {
  cursor: pointer;
  color: white;
  text-decoration: none; }

.Button:hover,
a .Button:hover {
  box-shadow: 1px 1px 10px #aaaaaa; }

.toggle-box {
  display: flex;
  align-items: center;
  justify-content: center; }
  .toggle-box .label {
    color: #6e6e6e;
    font-size: 10px; }

.ToggleButton .Button {
  margin: 0px 10px; }

.ToggleButton.off .Button {
  background-color: white;
  color: #6e6e6e; }

.ToggleButton.on .Button {
  color: white; }

